import React from "react";

export function TruthTable({ children, ...props }) {
  function processRow(row, isHeader) {
    let cells = row.split(",");

    cells = cells.map((cell) => {
      let animate = cell.includes("*") ? true : false;
      let divides = cell.includes("|") ? true : false;
      let main = cell.includes("!") ? true : false;

      if (isHeader === true) {
        return (
          `<th class='${animate ? "fragment" : ""} ${
            divides ? "divides" : ""
          }'>` +
          cell.replace(/[*|]/, "") +
          "</th>"
        );
      } else {
        return (
          `<td class='${animate ? "fragment" : ""} ${
            divides ? "divides" : ""
          } ${
            main ? "main" : ""
          }'>` +
          cell.replace(/[*!|]+/, "") +
          "</td>"
        );
      }
    });
    return "<tr>" + cells.join("") + "</tr>";
  }

  let header = "";
  let rows = children.props.children.split("\\newline");

  if (rows.length > 1) {
    header = rows.shift();

    header = processRow(header, true);
  }

  rows = rows.map(processRow, false);

  return (
    <table {...props} className="truth-table">
      <thead dangerouslySetInnerHTML={{ __html: header }} />
      <tbody dangerouslySetInnerHTML={{ __html: rows.join("") }} />
    </table>
  );
}
